<template>
    <v-container fluid>
        <div v-show="showCarousel" class="carousel-container">
            <div class="carousel">
                <div id="closeCarousel">
                    <v-icon
                        large
                        color="white"
                        style="cursor:pointer"
                        @click="closeCarousel"
                    >
                        mdi-close-circle
                    </v-icon>
                </div>
                <v-carousel
                    v-model="carousel"
                    hide-delimiters
                    :continuous="false"
                    height="100%"
                >
                    <v-carousel-item
                        eager
                        v-for="(img, index) in imgs"
                        :key="index"
                        class="v-carousel"
                    >
                        <v-img
                            eager
                            class="carousel-img"
                            :src="img"
                            contain
                        ></v-img>
            
                    </v-carousel-item>
                </v-carousel>
            </div>
        </div>
        <div id="gallery">
            <div class="img-container"
                v-for="(img, index) in imgs"
                :key="index"
            >
                <v-img
                    class="img"
                    :src="img"
                    @click="() => openCarousel(index)"
                ></v-img>
            </div>
        </div>
    </v-container>
</template>

<script>
import FrontSuspensionDec2021 from "@/assets/gallery/Front Suspension Origin DEC 2021.png"
import ChassisDec2021 from "@/assets/gallery/Origin Chassis SIDE VIEW Dec 2021.png"
import OriginDec2021 from "@/assets/gallery/Origin DEC 2021.png"
import OriginFrontDec2021 from "@/assets/gallery/Origin FRONT VIEW Dec 2021.png"
import OriginRearDec2021 from "@/assets/gallery/Origin REAR VIEW Dec 2021.png"
import OriginSideDec2021 from "@/assets/gallery/Origin SIDE VIEW Dec 2021.png"
import RtaSuspensionDec2021 from "@/assets/gallery/RTA Suspension Origin Dec 2021.png"
import OriginFeb2022 from "@/assets/gallery/Origin Feb 26th Assem.png"
import ShellRear from "@/assets/gallery/Origin Feb 26th Shell Rear.png"

export default {
    name: "Gallery",
    mounted() {
        window.addEventListener("keydown", e => {
            if(e.key == "Escape") {
                this.closeCarousel();
            }
        })
    },
    methods: {
        openCarousel(index) {
            this.carousel = index;
            this.showCarousel = true;
        },
        closeCarousel() {
            this.carousel = 0;
            this.showCarousel = false;
        }
    },
    data() {
        return {
            imgs: [
                FrontSuspensionDec2021,
                ChassisDec2021,
                OriginDec2021,
                OriginFrontDec2021,
                OriginRearDec2021,
                OriginSideDec2021,
                RtaSuspensionDec2021,
                OriginFeb2022,
                ShellRear
            ],
            carousel: 0,
            showCarousel: false
        }
    }
}
</script>

<style lang="scss" scopes>
#gallery {
    margin: 120px 70px 0 70px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#closeCarousel {
    position: absolute;
    z-index: 6;
    top: 10px;
    left: 10px;
    width: 50px;
    height: 50px;
}

.carousel-container {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.7);
}

.carousel {
    z-index: 5;
    position: relative;
    top: 0;
    left: 0;
    justify-self: center;
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.1);

    .v-carousel {
        display: flex;
        justify-content: center;
        align-items: center;
        max-height: 500px;
        max-width: 900px;

        .carousel-img {
            max-width: 100vw;
            max-height: 100vh;
        }
    }
}

.test {
    width: 100vw;
    height: 100vh;
}

.img-container {
    background: rgba(255, 255, 255, 0.06);
    margin: 10px;
}

.img {
    cursor: pointer;
    margin: 10px;
    max-width: 200px;
    max-height: 200px;
}
</style>